w-image-block::part(root) {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  display: flex;
  flex-shrink: 1;
  align-items: center;
  min-width: 120px;
  height: 120px;
  font-family: "Segoe UI", Roboto, "Helvetica Neue", arial, sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #444;
}
w-image-block::part(image) {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  flex-shrink: 0;
  margin: 0px 10px 0px 0px;
  width: 120px;
  height: 120px;
  border: 0px;
  border-radius: 50%;
  perspective: 1px;
  overflow: hidden;
}
w-image-block::part(text) {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 0px 24px 0px 16px;
  min-width: 0px;
  width: 100%;
  text-overflow: ellipsis;
  white-space: nowrap;
  text-align: left;
}
w-image-block::part(description) {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow: hidden;
  color: #323130;
  font-size: 20px;
}
w-image-block::part(secondary) {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow: hidden;
  color: #605e5c;
}